import axios from 'axios'
import router from '../../router'

const state = {
  sidebar: {
    opened: false,
  },
  config: {
    palette: {
      primary: '#4ae387',
      danger: '#e34a4a',
      info: '#4ab2e3',
      success: '#db76df',
      warning: '#f7cc36',
      white: '#fff',
      black: '#000',
      fontColor: '#34495e',
      transparent: 'transparent',
      lighterGray: '#ddd',
    },
  },
  isLoading: true,
  // Adding globla Varable
  user: {
    token: '',
    username: '',
    risklevel: '',
  },
  logged: false,
  loginMsg: '',
  registerMsg: '',
  haveSurvey: false,
  surveyMsg: '',
  industry: {
    industryName: '',
    industryCode: '',
  },
  shares: {
    sharesName: '',
    sharesSymbol: '',
  },
}

const mutations = {
  setLoading (state, isLoading) {
    state.isLoading = isLoading
  },
  loginisSuccess (state, data) {
    // console.log('mutation', username)
    state.loginMsg = ''
    state.user.username = data.username
    console.log(data.tempToken)
    state.user.token = data.tempToken
    state.logged = true
  },
  loginisFail (state, loginMsg) {
    state.loginMsg = loginMsg
    state.logged = false
  },
  registerisSuccess (state) {
    state.loginMsg = '#Register Success'
    state.logged = false
  },
  registerisFail (state, registerMsg) {
    state.registerMsg = registerMsg
    state.logged = false
  },
  Havesurvey (state) {
    state.haveSurvey = true
    state.surveyMsg = ''
  },
  Haventsurvey (state, surveyMsg) {
    state.haveSurvey = false
    state.surveyMsg = surveyMsg
  },
  setIndustry (state, { industryName, industryCode }) {
    state.industry.industryName = industryName
    state.industry.industryCode = industryCode
  },
  setShares (state, { sharesName, sharesSymbol }) {
    state.shares.sharesName = sharesName
    state.shares.sharesSymbol = sharesSymbol
  },
}

const actions = {

  async loginapi ({ commit, dispatch }, userData) {
    const { username, password } = userData
    const response = await axios({
      method: 'post',
      url: 'http://47.89.11.48:8081/user/auth',
      data: JSON.stringify({ username, password }),
    })
    // console.log(response)

    if (response.data.status === '0') {
      commit('loginisFail', '#Wrong username or password')
    } else {
      const tempToken = response.data.msg
      console.log('TOKEN', tempToken)
      commit('loginisSuccess', { username, tempToken })
      localStorage.setItem('cookie_username', username)
      localStorage.setItem('cookie_token', tempToken)
      dispatch('getrisklevelapi', { tempToken }) // getrisklevelapi
    }
  },

  async signupapi ({ commit }, userData) {
    const { username, password } = userData
    const response = await axios({
      method: 'post',
      url: 'http://47.89.11.48:8081/user/register',
      data: JSON.stringify({ username, password }),
    })

    if (response.data.status === '0') {
      commit('registerisFail', '#User is already exist')
    } else {
      commit('registerisSuccess')
      router.push('login')
    }
  },

  async takesurveyapi ({ commit, state }, userData) {
    console.log(userData)
    console.log(state)
    const {
      risklevel,
      questionArray,
    } = userData
    await axios({
      method: 'post',
      url: 'http://47.89.11.48:8081/user/survey',
      data: JSON.stringify({
        token: state.user.token,
        risklevel,
        survey: questionArray,
      }),
    })
      .then(response => console.log(response))
  },

  async getrisklevelapi ({ commit }, userData) {
    const { tempToken } = userData
    console.log('token: ', tempToken)
    const response = await axios({
      method: 'post',
      url: 'http://47.89.11.48:8081/user/getrisklevel',
      data: JSON.stringify({ token: tempToken }),
    })

    if (response.data.status === '0') {
      commit('Haventsurvey', '#Click login once to do the Survey now')
      router.push('take-survey')
    } else {
      commit('Havesurvey')
      router.push('dashboard')
    }
  },

  async setIndustryAPI ({ commit }, userData) {
    const { industryName, industryCode } = userData
    commit('setIndustry', { industryName, industryCode })
  },

  async setSharesAPI ({ commit }, userData) {
    const { sharesName, sharesSymbol } = userData
    commit('setShares', { sharesName, sharesSymbol })
  },
}

export default {
  state,
  mutations,
  actions,
}
